<template>
  <div class="_PG-index">
    <img src="./assets/image/banner.jpg" alt="" class="CS-banner_img">
    <div class="CS-op_container">
      <div class="cs-oc-top">
        <div class="cs-oct-btn_container">
          <button class="cs-oct-class_btn"
                  v-for="(item, index) in classList"
                  @click="handleSelectClass(item)"
                  :key="index"
                  :class="{'active': currentClass.name === item.name}"
          >
            {{item.name}}
          </button>
        </div>
      </div>
      <div class="cs-op-bottom">
        <img src="./assets/image/teacher.png" alt="" class="cs-opb-guide_img" v-if="!currentClass.name">
        <div class="cs-opb-code_container" v-if="currentClass.name">
          <h2 class="cs-opbc-title">{{currentClass.name}}</h2>
          <p class="cs-opbc-text">长按识别二维码<img class="cs-opbct_z" src="./assets/image/zhixiang.png" />进群学习</p>
          <img :src="currentClass.code" alt="" class="cs-opbc-qrcode">
          <p class="cs-opbc-safe_text" ref="safeText"><img src="./assets/image/safe.png"
                                                           class="cs-opbct_safe" />此二维码已通过安全认证，可放心扫码
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      classList: window.class_code_config,
      currentClass: {}
    }
  },
  methods: {
    handleSelectClass (currentClass) {
      this.currentClass = currentClass
      this.$nextTick(() => {
        this.$refs.safeText.scrollIntoView({
          behavior: 'smooth',
          block: 'start'
        })
      })
    }
  }
}
</script>

<style lang="less">
  @transparent: rgba(255, 255, 255, 0);
  .border-circle(@position) when (@position='tl') {
    left: -15px;
    top: -15px;
  }

  .border-circle(@position) when (@position='tr') {
    right: -15px;
    top: -15px;
  }

  .border-circle(@position) when (@position='bl') {
    left: -15px;
    bottom: -15px;
  }

  .border-circle(@position) when (@position='br') {
    right: -15px;
    bottom: -15px;
  }

  .border-circle (@position: 'tl') {
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #FAF7E1;
    z-index: 1;
  }

  ._PG-index {
    padding: 18px;
    position: absolute;
    min-height: 100%;
    width: 100%;
    background-color: #FAF7E1;
    .CS-banner_img {
      width: 100%;
      height: auto;
      border-radius: 8px;
    }
    .CS-op_container {
      margin-top: 18px;
      background-color: rgba(255, 255, 255, 0);
    }
    .cs-oc-top {
      padding-left: 25px;
      padding-right: 25px;
      padding-bottom: 25px;
      position: relative;
      min-height: 200px;
      background-color: #fff;
      border-bottom: 3px dashed #F7F7F7;
      &:before {
        .border-circle('bl');
      }
      &:after {
        .border-circle('br')
      }
    }
    .cs-oct-btn_container {
      display: flex;
      width: 100%;
      justify-content: space-between;
      flex-wrap: wrap;
      &:after {
        content: "";
        width: 200px;
      }
    }
    .cs-oct-class_btn {
      margin-top: 25px;
      width: 200px;
      height: 70px;
      background-color: #E8E8E8;
      font-size: 25px;
      color: #333;
      border: none;
      border-radius: 4px;
      &.active {
        background-color: #ED5200;
        color: #fff;
      }
    }
    .cs-op-bottom {
      padding-top: 25px;
      padding-bottom: 130px;
      position: relative;
      display: flex;
      justify-content: center;
      min-height: 200px;
      background-color: #fff;
      &:before {
        .border-circle('tl');
      }
      &:after {
        .border-circle('tr')
      }
    }
    .cs-opb-guide_img {
      width: 480px;
      height: 404px;
    }
    .cs-opb-code_container {
      display: flex;
      width: 100%;
      flex-direction: column;
      align-items: center;
    }
    .cs-opbc-title {
      padding: 0 10px;
      font-size: 30px;
      display: inline-block;
      color: #333;
      border-bottom: 6px solid #EC4C00;
    }
    .cs-opbc-text {
      margin-top: 20px;
      font-size: 20px;
      color: #707684;
    }
    .cs-opbct_z {
      width: 30px;
    }
    .cs-opbc-qrcode {
      margin-top: 30px;
      width: 280px;
      height: 280px;
      outline: 6px dashed #EC4C00;
    }
    .cs-opbc-safe_text {
      bottom: 0;
      left: 0;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 80px;
      font-size: 25px;
      color: #36960a;
      background-color: #EFFFF0;
    }
    .cs-opbct_safe {
      margin-right: 5px;
      width: 30px;
    }
  }
</style>



